.app {
  width: 100%;
  height: 110vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar-toggler-icon {
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;
  background-image: var(--bs-navbar-toggler-icon-bg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  font-size: 10px;
  border-radius: 50%;
}

.iconLogo {
  height: 40px;
  width: 40px;
  border-radius: 50px;
}

.app:before {
  content: "";
  background: url("./assets/backgroundimage.jpg") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.maincontent {
  text-align: center;
}

.location {
  height: 30px;
  width: 200px;
  padding: 0.7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background: rgba(255, 255, 255, 0.1);
  color: #f8f8f8;
}

#enterlocation {
  width: 30px;
  height: 30px;
  font-size: 12px;
  transition-duration: 0.4s;
  border: transparent;
}

.content {
  max-width: 400px;
  height: 300px;
  margin: auto;
  position: relative;
  top: 10%;
}

.weathercelsius {
  padding-left: 60px;
}

.bottomcontent {
  height: 70px;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
}

.aboutwise {
  padding-top: 100px;
}
